import React, { useEffect, useRef, useState } from "react";
import "react-dropzone-uploader/dist/styles.css";
import Dropzone from "react-dropzone-uploader";
import Loader from "../assets/loader/loader.gif";

const API_ENDPOINT =
  "https://s8hr230iz1.execute-api.ap-southeast-1.amazonaws.com";

function formatDate(date) {
  const pad = (num) => num.toString().padStart(2, "0");

  const year = date.getFullYear();
  const month = pad(date.getMonth() + 1); // Months are 0-indexed
  const day = pad(date.getDate());
  const hours = pad(date.getHours());
  const minutes = pad(date.getMinutes());

  return `${year}-${month}-${day}_${hours}${minutes}`;
}

const Uploader = () => {
  const [status, setStatus] = useState("");
  const [change, setChange] = useState("");
  const [numFiles, setNumFiles] = useState(0);
  const [hasError, setHasError] = useState(false);
  const ref = useRef();

  useEffect(() => {
    const button = document.querySelector(".dzu-input");
    const callback = () => console.log("upload click");
    button.addEventListener("click", callback);

    return () => {
      button.removeEventListener("click", callback);
    };
  }, []);

  const handleChangeStatus = ({ meta, remove }, status) => {
    console.log(JSON.stringify(meta));
    setChange(status);
    switch (status) {
      case "done":
        setNumFiles((prev) => ++prev);
        break;
      case "removed":
        setNumFiles((prev) => --prev);
        break;
    }
  };

  const handleSubmit = async (files, allFiles) => {
    setHasError(false);
    setStatus("Uploading");

    for (let f of files) {
      const file = f.file;
      const partSize = 5 * 1024 * 1024; // 5MB per part
      const totalParts = Math.ceil(file.size / partSize);
      const response = await fetch(`${API_ENDPOINT}/multipart`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          contentType: file.type,
          partCount: totalParts,
        }),
      });
      const { uploadId, key, presignedUrls } = await response.json();
      const uploadPart = async (part, index) => {
        const start = index * partSize;
        const end = Math.min(start + partSize, file.size);
        const blob = file.slice(start, end);

        const res = await fetch(part.url, {
          method: "PUT",
          body: blob,
        });

        const etag = res.headers.get("ETag");
        return {
          ETag: etag?.replace(/"/, ""), // S3 returns ETag with quotes
          PartNumber: part.partNumber,
        };
      };

      const uploadResults = await Promise.all(
        presignedUrls.map((part, index) => uploadPart(part, index))
      );

      const completeRsp = await fetch(`${API_ENDPOINT}/complete-upload`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          uploadId,
          key,
          parts: uploadResults,
        }),
      });

      //   // GET request: presigned URL
      //   const response = await axios({
      //     method: "GET",
      //     url: API_ENDPOINT,
      //   });

      //   // PUT request: upload file to S3
      //   const result = await fetch(response.data.uploadURL, {
      //     method: "PUT",
      //     body: f["file"],
      //   });

      if (completeRsp.status === 200) setStatus("Upload completed");
      if (completeRsp.status === 403) setHasError(true);
    }

    allFiles.forEach((f) => f.remove());
  };

  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        padding: "1rem",
      }}
    >
      {status === "Uploading" && <img src={Loader} style={{ width: "100%" }} />}
      {status !== "" && <h1 style={{ color: "green" }}>{status}</h1>}
      {hasError && <h1 style={{ color: "red" }}>An error has occurred</h1>}
      <h2>Number of files: {numFiles}</h2>
      <Dropzone
        ref={ref}
        onChangeStatus={handleChangeStatus}
        onSubmit={handleSubmit}
        maxFiles={1}
        canCancel={false}
        styles={{ dropzone: { minHeight: 200, maxHeight: 250 } }}
        inputContent="Drop A File"
      />
    </div>
  );
};

export default Uploader;
