import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';


const API_ENDPOINT =
  "https://s8hr230iz1.execute-api.ap-southeast-1.amazonaws.com";
  
function sendLogToServer(level, message) {
  fetch(`${API_ENDPOINT}/logging`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ level, message, timestamp: new Date().toISOString() })
  }).catch(err => {
    // You might not want to throw here
    // console.error("Failed to send log to server", err);
  });
}

['log', 'warn', 'error'].forEach(level => {
  const original = console[level];
  console[level] = function(...args) {
    original.apply(console, args);
    sendLogToServer(level, args.join(" "));
  };
});

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
